.header {
  min-height: 9.375rem;
  background: #ffffff;
  display: flex;
  justify-content: start;
  padding: 0 3.125rem 0 6.25rem;
  position: relative;
}

.header_descr {
  margin-top: 1.875rem;
  width: 18.75rem;
}

.header_descr_title {
  font-weight: 700;
  font-size: 1.5rem;
  line-height: 1.75rem;
  color: #000000;
  margin-bottom: 0.3125rem;
}

.header_descr_subtitle {
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1rem;
  color: #000000;
  margin-top: 0.0625rem;
}

.header::after {
  content: '';
  width: 100%;
  height: 0.125rem;
  background: #000000;
  position: absolute;
  left: 0;
  top: 9.25rem;
}

.header_img {
  flex-grow: 1;
  display: flex;
  justify-content: end;
  margin-right: 3.125rem;
}

.account {
  width: 8.125rem;
  min-height: 9.375rem;
}

.account_logo {
  background: #C4C4C4;
  border-radius: 100%;
  width: 4.375rem;
  height: 4.375rem;
  margin-left: 1.875rem;
  margin-top: 0.625rem;
}

.btn {
  padding: 0;
  font-weight: 400;
  color: #ffffff;
  font-size: 0.875rem;
  line-height: 1rem;
  border: none;
  border-radius: 3.125rem;
  width: 6.875rem;
  height: 1.875rem;
  cursor: pointer;
}

.btn_account {
  background: #706565;
  margin-top: 0.9375rem;
  margin-left: 0.625rem;
}

.menu {
  padding: 0;
  width: 15rem;
  min-height: calc(100vh - 190px);
  padding: 1.25rem 1.875rem;
}

.menu ul {
  list-style: none;
  width: 15rem;
  padding: 0;
}

.menu-group {
  padding-left: 0.625rem;
}

.menu-item {
  min-height: 1.875rem;
  font-weight: 400;
  color: #000000;
  line-height: 1.875rem;
  margin-top: 1.25rem;
  position: relative;
}

.menu-item a {
  display: block;
  text-decoration: none;
  color: #000000;
  padding-left: 0.625rem;
  cursor: pointer;
}

.menu-item a:hover {
  background: #C4C4C4;
}

.menu-item::before {
  content: '';
  width: 15rem;
  height: 0.125rem;
  background: #000000;
  position: absolute;
  top: 1.75rem;
  left: 0;
}

.menu-item__active {
  background: #000000;
}

.menu-item__active a {
  color: #ffffff;
}

.menu-item__active a:hover {
  background: #000000;
}

.menu-subitem {
  min-height: 1.875rem;
  font-weight: 400;
  color: #000000;
  line-height: 1.875rem;
  position: relative;
}

.menu-subitem a {
  padding-left: 1.875rem;
  display: block;
  text-decoration: none;
  color: #000000;
  cursor: pointer;
}

.menu-subitem a:hover {
  background: #C4C4C4;
}

.menu-subitem::before {
  content: '';
  width: 0.625rem;
  height: 0.625rem;
  border-radius: 3.125rem;
  background: #000000;
  position: absolute;
  top: 0.625rem;
  left: 0.625rem;
}

.menu-subitem__active {
  background: #000000;
}

.menu-subitem__active a {
  color: #ffffff;
}

.menu-subitem__active a:hover {
  background: #000000;
}

.menu-subitem__active::before {
  background: #ffffff;
}

.menu::after {
  content: '';
  width: 0.125rem;
  height: calc(100% - 150px);
  background: #000000;
  position: absolute;
  top: 9.375rem;
  left: 18.625rem;
}